"use strict";

import React, { useState } from "react";
import Container from "@material-ui/core/Container";
import Typography from "@material-ui/core/Typography";
import Table from "@material-ui/core/Table";
import TableBody from "@material-ui/core/TableBody";
import TableCell from "@material-ui/core/TableCell";
import TableContainer from "@material-ui/core/TableContainer";
import TableHead from "@material-ui/core/TableHead";
import TableRow from "@material-ui/core/TableRow";
import InputLabel from "@material-ui/core/InputLabel";
import FormControl from "@material-ui/core/FormControl";
import Select from "@material-ui/core/Select";
import Paper from "@material-ui/core/Paper";
import { withStyles } from "@material-ui/core/styles";
import Grid from "@material-ui/core/Grid";
import MaterialTable, { MTableToolbar } from "material-table";
import Ad from "../src/Ad";
import Hint from "../src/Hint";

const styles = (theme) => ({
  root: {
    display: "flex",
    flexDirection: "column",
  },
  main: {
    marginTop: theme.spacing(2),
  },
  table: {
    marginTop: theme.spacing(1),
  },
  formField: {
    width: "200px",
  },
});

class App extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      makes: [
        "Audi",
        "Ford",
        "Hyundai",
        "Jaguar",
        "Kia",
        "Nissan",
        "Porsche",
        "Polestar",
        "Rivian",
        "Tesla",
      ],
      models: [],
      make: "",
      model: "",
      year: "",
      data: [],
    };
  }

  handleMakeChange = (event) => {
    this.setState({ make: event.target.value });
    this.loadData(event.target.value, null, true);
    if (!event.target.value) {
      // no make selected, clear models
      this.setState({ models: [] });
    }
  };

  handleModelChange = (event) => {
    this.setState({ model: event.target.value });
    this.loadData(this.state.make, event.target.value, false);
  };

  loadModels = (data) => {
    let modelMap = new Map();
    data.forEach((element) => {
      if (element.Model) {
        // parse model identifier
        let model = element.Sort.split("-")[0];
        modelMap.set(element.Model, model);
      }
    });
    // create array for data
    let models = [];
    modelMap.forEach(function (value, key) {
      models.push({ key: value, value: key });
    });
    this.setState({ models: models });
  };

  loadData = (make, model, shouldLoadModels) => {
    this.setState({ data: [] });
    if (make) {
      var uri = make;
      if (model) {
        uri += "/" + model;
      }
      fetch("http://localhost:8000/vehicles/" + uri)
        // fetch("https://hofpnvfyu2.execute-api.us-east-1.amazonaws.com/vehicles/" + uri)
        .then((res) => res.json())
        .then((data) => {
          this.setState({ data: data });
          if (shouldLoadModels) {
            this.loadModels(data);
          }
        })
        .catch(console.log);
    }
  };

  render() {
    const { classes } = this.props;
    return (
      <div className={classes.root}>
        <Container component="main" className={classes.main} maxWidth="lg">
          <Grid container spacing={2}>
            <Grid item>
              <Typography variant="h5" component="h1" gutterBottom>
                Electric Vehicle Search
              </Typography>
            </Grid>
            <Grid item>
              <FormControl variant="outlined">
                <InputLabel htmlFor="make">Make</InputLabel>
                <Select
                  native
                  value={this.state.make}
                  onChange={this.handleMakeChange}
                  label="Make"
                  inputProps={{
                    id: "make",
                  }}
                >
                  <option aria-label="None" value="" />
                  {this.state.makes.sort().map((x, index) => {
                    return (
                      <option key={x} value={x}>
                        {x}
                      </option>
                    );
                  })}
                </Select>
              </FormControl>
              <FormControl variant="outlined" className={classes.formField}>
                <InputLabel htmlFor="model">Model</InputLabel>
                <Select
                  native
                  value={this.state.model}
                  onChange={this.handleModelChange}
                  disabled={this.state.models.length === 0}
                  label="Model"
                  inputProps={{
                    id: "model",
                  }}
                >
                  <option aria-label="None" value="" />
                  {this.state.models.sort().map((x) => {
                    return (
                      <option key={x.key} value={x.key}>
                        {x.value}
                      </option>
                    );
                  })}
                </Select>
              </FormControl>
            </Grid>
            <Grid item>
              <Typography variant="subtitle1">
                Research electric vehicles with the most up-to-date
                specifications from Tesla, Audi, Rivian, Ford, Volkswagen,
                Polestar, Volvo, BMW, and other battery electric vehicle (BEV)
                automakers.
              </Typography>
            </Grid>
          </Grid>

          <div className={classes.table}>
            <MaterialTable
              columns={[
                {
                  title: "Make",
                  field: "Make",
                  headerStyle: { whiteSpace: "nowrap" },
                },
                {
                  title: "Model",
                  field: "Model",
                  headerStyle: { whiteSpace: "nowrap" },
                },
                {
                  title: "Year",
                  field: "Year",
                  type: "numeric",
                  width: 50,
                  headerStyle: { whiteSpace: "nowrap" },
                },
                {
                  title: "Trim",
                  field: "Variant",
                  headerStyle: { whiteSpace: "nowrap" },
                },
                {
                  title: "Range (mi)",
                  field: "Range",
                  type: "numeric",
                  width: "1%",
                  headerStyle: { whiteSpace: "nowrap" },
                },
                {
                  title: "Accel (0-60)",
                  field: "Accel",
                  type: "numeric",
                  width: "1%",
                  headerStyle: { whiteSpace: "nowrap" },
                },
                {
                  title: "Power (hp)",
                  field: "PowerHP",
                  type: "numeric",
                  width: "1%",
                  headerStyle: { whiteSpace: "nowrap" },
                },
                {
                  title: "Torque",
                  field: "Torque",
                  type: "numeric",
                  headerStyle: { whiteSpace: "nowrap" },
                  width: "1%",
                },
              ]}
              data={this.state.data}
              options={{
                draggable: false,
                sorting: true,
                search: true,
                padding: "dense",
                showTitle: false,
                toolbar: true,
                paging: false,
                headerStyle: {
                  backgroundColor: "#1976d2",
                  color: "#FFF",
                },
                searchFieldAlignment: "left",
                rowStyle: (rowData, index) => {
                  if (index % 2) {
                    return { backgroundColor: "#f2f2f2" };
                  }
                },
              }}
              localization={{
                body: {
                  emptyDataSourceMessage:
                    'Select a "Make" and "Model" to research electric vehicle data',
                },
                toolbar: {
                  searchPlaceholder: "Find within results",
                },
              }}
            />
          </div>
          <Hint />
          <Ad />
        </Container>
      </div>
    );
  }
}

export default withStyles(styles, { withTheme: true })(App);
